import React, { useCallback, useEffect, useState } from "react";
import InnerFrame from "./InnerFrame";
import { Button,ButtonToolbar  } from 'rsuite';
import { useNavigate, useParams ,useLocation } from "react-router-dom";
import 'rsuite/dist/rsuite.min.css';

const Aftertest = () => {
    const navigate = useNavigate();
    const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0); //
    const [userAnswers, setUserAnswers] = useState(null); // 
    const [showResult, setShowResult] = useState(false); // 
    const params = useParams();
    const location = useLocation();
    const [correctAnswers, setCorrectAnswers] = useState(0); // 
    const [currentPage, setCurrentPage] = useState("nicknameInput"); // 
    const [account,setaccount] = useState('Null'); // //
    const [quizData, setQuizData] = useState([]);

    const [pretest,setpretest] = useState(0); //
    const total = 7; //

    const onStuImageClick = useCallback(() => {
      navigate("/schoolpage", { state: { account: account,pretest:pretest } });
      }, [navigate, account]);

    // 
    const onTeacherImageClick = useCallback(() => {
    navigate("/homepage");
    }, [navigate]);

    useEffect(() => {
      if (location.state) {
        setaccount(location.state.account);
        setpretest(location.state.pretest);
        console.log(location.state.account,location.state.pretest)
      }
    }, [location.state]);

    const handleStartQuiz = useCallback(async () => {
      try {
          const response = await fetch(`http://localhost:3001/api/quiz/after/`);
          if (!response.ok) {
              throw new Error('Failed to fetch quiz data');
          }
          const quizData = await response.json();
          setCurrentPage("quiz");
          setQuizData(quizData); //  quizData 
          console.log(quizData)
          
      } catch (error) {
          console.error('Error fetching quiz data:', error);
          // 
      }
  }, []);
  
  
    useEffect(() => {
      const checkAnswers = () => {
        if (quizData && userAnswers !== null && showResult !== null) {
          // 
          const answeredCorrectly = quizData[currentQuestionIndex][userAnswers] === showResult;
          if (answeredCorrectly) {
            setCorrectAnswers(prevCorrectAnswers => prevCorrectAnswers + 1);
          }
        }
      };
      checkAnswers();
    }, [quizData, userAnswers, showResult, currentQuestionIndex]);

    const handleAns = useCallback((selectedOption) => {
    setUserAnswers(selectedOption);
    setShowResult(quizData[currentQuestionIndex].ans);
    // 
    setCurrentPage("correctAnswer");
    }, [quizData, currentQuestionIndex]);

    const handleNextQuestion = () => {
    if (currentQuestionIndex < total - 1) {
        setUserAnswers(null) //
        setShowResult(false); // 
        setCurrentQuestionIndex(prevIndex => prevIndex + 1); // 
        setCurrentPage("quiz"); // 
    } else {
        setUserAnswers(null)  //
        setShowResult(true); // 
        setCurrentPage("result"); // 
        console.log(correctAnswers)
    }
    };

    const renderPage = () => {
    switch (currentPage) {
        case "nicknameInput":
        return (
            // 
            <div className="frame-wrapper">
                <div className="frame-parent1">
                    <div className="wrapper">
                    <div className="div12"><h1></h1></div>
                    </div>
                    <div className="div13" style={{textAlign:'left',}}>
                    <div className="frame-parent_b" style={{flexDirection:'colum'}}>
                        <p></p>
                    <ButtonToolbar>
                    <Button size="lg" color="cyan" block style={{height:50,fontSize:20,margin:13,color:'black'}} onClick={handleStartQuiz} > </Button>
                    </ButtonToolbar>
                    </div>
                     <p style={{ color: '#F0AFAB' }}></p>
                    </div>
                </div>
                </div>  
        );
        case "quiz":
        return (
            // 
            <div className="frame-wrapper">
                <div className="frame-parent1">
                    <div className="wrapper">
                    <div className="div12">
                        <h2>
                        <React.Fragment >
                        {quizData[currentQuestionIndex].que}
                        </React.Fragment>
                    </h2>
                    </div>
                    </div>
                    <div className="div13" style={{textAlign:'left',}}>
                    <div className="frame-parent_b" style={{flexDirection:'colum'}}>
                        <div className="div13" style={{textAlign:'left',}}>
                        <div className="frame-parent_b" style={{flexDirection:'column',color:'#95f293'}}>
                            {/*  */}
                            <ButtonToolbar  value={userAnswers} onChange={(value)=>{setUserAnswers(value);console.log(value)}}>
                            {quizData && ['A', 'B', 'C', 'D'].map((option) => (
                            <Button style={{height:80,fontSize:25,color:'whitesmoke',textAlign:'left'}} appearance="subtle" color="cyan" block 
                            value={option} key={option} onClick={()=>{handleAns(option)}}>
                                {quizData[currentQuestionIndex][option]}</Button>
                            ))}
                            </ButtonToolbar></div> 
                        </div>
                    </div>  
                    </div>
                </div>
                </div>  
        );
        case "correctAnswer":
        return (
            // 
            <div className="frame-wrapper">
            <div className="frame-parent1">
            <div className="wrapper">
                <div className="div12">
                <h2 style={{}}>
                    <React.Fragment >
                    {quizData[currentQuestionIndex].que}
                    </React.Fragment>
                </h2>
                </div>
            </div>
            <div className="div13" style={{textAlign:'left',}}>
                <div className="frame-parent_b" style={{flexDirection:'colum',color:'#00000'}}>
                {['A', 'B', 'C', 'D'].map((option) => (
                        <p key={option} style={{fontSize:26,color:'whitesmoke'}}>{quizData[currentQuestionIndex][option]}
                        </p>
                    ))}
                </div>  
                <React.Fragment >
                  <div style={{alignContent:'center'}}>
                    <p style={{fontSize:26 ,alignContent:'center' , color: '#F0AFAB'}}>{showResult}</p><p style={{fontSize:26,alignContent:'center', color:'black'}}>{quizData[currentQuestionIndex][userAnswers]}</p>
                    <Button size="lg" color="cyan" block style={{height:50,fontSize:20,margin:13,color:'black'}} onClick={handleNextQuestion}>
                    {currentQuestionIndex < total - 1 ? "" : ""}</Button>                
                  </div>
                </React.Fragment>
            </div>
            </div>
        </div>  
        );
        case "result":
        return (
            // 
            <div className="frame-wrapper">
            <div className="frame-parent1">
            <div className="wrapper">
                <div className="div12">
                <h2></h2>
                </div>
            </div>
            <div className="div13" style={{textAlign:'left',}}>
                <div className="frame-parent_b" style={{flexDirection:'colum',}}></div>  
                <p></p>
                <p></p>
                <p style={{color: '#F0AFAB'}}>{pretest}</p>
                <p style={{color: '#F0AFAB'}}>{account}{correctAnswers}</p>
            </div>
            <Button onClick={onStuImageClick} block color="cyan" size="lg" style={{color: '#000000',height:80,width: 500,alignItems:'center', fontSize: 40, alignSelf:'center'}}></Button>
            </div>
        </div>  
        );
        default:
        return null;
    }
    };

  return (
    <div className="schoolquiz">
      <img className="bkground-icon6" alt="" src="/bkground@2x.png" />
      <InnerFrame prop="" />
      <div className="frame-div">
        <div className="bk-frame-parent">
          <div className="bk-frame">
            <div className="rectangle-parent">
              <div className="frame-child" />
              {renderPage()}
            </div>
          </div>
          <div className="wrapper-stu">
            <img
              className="stu-icon1"
              loading="eager"
              alt=""
              src="/stu@2x.png"
            />
          </div>
        </div>
        <img
          className="teacher-icon3"
          loading="eager"
          alt=""
          src="/teacher@2x.png"
          onClick={onTeacherImageClick}
        />
      </div>
    </div>
  );
};

export default Aftertest;
